<template>
  <div class="square_import">
    <h1>Import text from pokemon showdown and select a tier to proceed</h1>
    <textarea
      class="square_import_text"
      v-model="rawData"
    ></textarea>
    <div class="square_import_format">
      <div
        v-for="(tier, key) in Object.keys(dex)"
        class='square_import_format_tier'
        :class="{'square_import_format_tier-active': selectedTier==tier}"
        @click.self="selectedTier=tier"
        :key="key"
      >
        {{tier}}
      </div>
    </div>
    <button
      @click="upload(false)"
      class="square_upload square_upload--small"
    >Save</button>
  </div>
</template>

<script>
const parseTeam = () => import("@/services/teamParser");
import axios from "axios";

export default {
  data() {
    return {
      selectedTier: "",
      rawData: "",
      loading: false,
      localData: [],
    };
  },
  methods: {
    upload(getData) {
      if (this.selectedTier !== false) {
        this.loading = true;
        parseTeam().then(p => {
          let parsed = {};
          parsed.pokemon = p.parseTeam(this.rawData);

          for (let index in parsed.pokemon) {
            if (parsed.pokemon[index].level === undefined)
              parsed.pokemon[index].level = this.dex[this.selectedTier];
          }
          parsed.raw = this.rawData;
          parsed.tier = {
            defaultLevel: this.dex[this.selectedTier],
            alias: this.selectedTier
          };
          parsed.getData = getData;

          axios.post("/mysquad", parsed).then(res => {
            if (res.data.sucess) {
              let teamMembers = [];
              res.data.response.pokemon.forEach(pkmn => {
                teamMembers.push({ name: pkmn.data.slug });
              });

              axios
                .post("/mysquad/upload", {
                  raw: res.data.response.raw,
                  tier: res.data.response.tier,
                  teamMembers: teamMembers
                })
                .then(response => {
                  if (response.data.code === 1) {
                    this.localData.push(response.data.id)
                    localStorage.setItem('saved', JSON.stringify(this.localData))
                    this.loading = false
                    this.$router.push({path: response.data.path})
                  }
                })
                .catch(err => {
                  alert(err);
                });
            } else {
              alert(res.data.message);
            }
          });
        });
      }
    }
  },
  created(){
    let saved = JSON.parse(localStorage.getItem('saved'))
    if(saved!==null){
      this.localData = saved
    }
  }
};
</script>