<template>
    <div>
        <section v-for="(team, key) in uploaded" :key="key">
            <img v-for="(pkmn, k) in team.teamMembers" :key="k" :src="`/2d/${pkmn.name}.png`">
            <button @click="$router.push({path:'/mysquad/shared/'+team.tier+'/'+team._id})">edit</button>
        </section>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return {
            uploaded:[]
        }
    },
    created(){
        const saved = JSON.parse(localStorage.getItem('saved'))
        if(saved){
            axios.post("/mysquad/myuploads", saved)
                .then(res=>{
                    this.uploaded = res.data
                })
                .catch(err => {
                    console.log(err)
                })
        }
    }
}
</script>
