<template>
  <section class="mysquad">
    <BreakMyTeam
      :latestEntries='le'
    />
  </section>
</template>


<script>
import axios from "axios";
import BreakMyTeam from "@/components/BreakMyTeam";

export default {
  title: "Battle Square",
  components: {
    BreakMyTeam: BreakMyTeam
  },
  data() {
    return {
      team: "",
      parsed: false,
      selectedTier: false,
      loading: false,
      tiers: {
        ou: 100,
        vgc: 50,
        bss: 50,
        ru: 100,
        nu: 100,
        ubers: 100,
        monotype: 100,
        uu: 100
      },
      le: []
    };
  },
  created() {
    this.loading = false;
    axios.post("/mysquad/shared/latest").then(res => {
      this.le = res.data.teams;
    });
  }
};
</script>