<template>
  <section class="square">
    <div class="square__options">
      
      <button>
        Latest entries
      </button>
      
      <button
        @click="activateModal('yourUploads', 'modal--long')"
        class="square_upload"
      >
        Your uploads
      </button>

      <button
        class="square_upload"
        @click="activateModal('importSlot', 'modal--large')"
      >new team</button>
    
    </div>
    
    <LatestEntries :teams='latestEntries' />

    <transition name='bounce'>
      <Modal
        v-if="uploadModal"
        @close-my-modal='uploadModal=false'
        :sizeClass='modalSize'
      >
        <component :is="selectedComponent"></component>
      </Modal>
    </transition>

  </section>
</template>

<script>
import LatestEntries from "./_LatestTeams";
import importSlot from "./_importSlot";
import yourUploads from "./_yourUploads";
import Modal from "../Modal";

export default {
  data() {
    return {
      uploadModal: false,
      modalSize: "",
      selectedComponent: ""
    };
  },
  components: {
    importSlot,
    yourUploads,
    LatestEntries,
    Modal
  },
  props: ["latestEntries"],
  methods: {
    activateModal(component, modalSize) {
      this.selectedComponent = component;
      this.modalSize = modalSize;
      this.uploadModal = true;
    }
  }
};
</script>

